import Vue from 'vue'
import VueRouter from 'vue-router'
import GTEvents from '../views/GTEvents.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'GTEvents',
    component: GTEvents,
    query: { gta_url: "https://geotrekdemo.ecrins-parcnational.fr" }
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
